<template><section>
<h2 id="checkbox-duo-xuan-kuang"><a class="header-anchor" href="#checkbox-duo-xuan-kuang" aria-hidden="true">¶</a> Checkbox 多选框</h2>
<p>一组备选项中进行多选。</p>
<h3 id="ji-chu-yong-fa"><a class="header-anchor" href="#ji-chu-yong-fa" aria-hidden="true">¶</a> 基础用法</h3>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-checkbox v-model="checked">复选框</fm-checkbox>


</div>
                                                <p>基础的多选框用法。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked"</span>&gt;</span>复选框<span class="hljs-tag">&lt;/<span class="hljs-name">fm-checkbox</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">checked</span>: <span class="hljs-literal">false</span>,
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="jin-yong-zhuang-tai"><a class="header-anchor" href="#jin-yong-zhuang-tai" aria-hidden="true">¶</a> 禁用状态</h3>
<p>多选框的不可用状态</p>
<docs-demo-block> 
                                                <div class="source" slot="source"><fm-checkbox v-model="checked1" disabled>不可用</fm-checkbox>
<fm-checkbox v-model="checked2" disabled>选中不可用</fm-checkbox>


</div>
                                                <p>设置 <code>disabled</code> 属性即可。</p>
<pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">fm-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked1"</span> <span class="hljs-attr">disabled</span>&gt;</span>不可用<span class="hljs-tag">&lt;/<span class="hljs-name">fm-checkbox</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">fm-checkbox</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"checked2"</span> <span class="hljs-attr">disabled</span>&gt;</span>选中不可用<span class="hljs-tag">&lt;/<span class="hljs-name">fm-checkbox</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data () {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">disabled</span>: <span class="hljs-literal">true</span>,
                <span class="hljs-attr">checked1</span>: <span class="hljs-literal">false</span>,
                <span class="hljs-attr">checked2</span>: <span class="hljs-literal">true</span>
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="chi-cun"><a class="header-anchor" href="#chi-cun" aria-hidden="true">¶</a> 尺寸</h3>
<p>指定多选框的大小</p>
<docs-demo-block> 
                                                <div class="source" slot="source">    <fm-checkbox label="小尺寸" size="small"></fm-checkbox>
    <fm-checkbox label="中尺寸" size="medium"></fm-checkbox>
    <fm-checkbox label="大尺寸" size="large"></fm-checkbox>
</div>
                                                <p>可通过 <code>size</code> 属性指定输入框的尺寸，默认大小是 <code>small</code></p>
<pre v-pre=""><code class="hljs language-html">    <span class="hljs-tag">&lt;<span class="hljs-name">fm-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"小尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"中尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"medium"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-checkbox</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">fm-checkbox</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"大尺寸"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">fm-checkbox</span>&gt;</span>
</code></pre>
</docs-demo-block>
<h3 id="shu-xing"><a class="header-anchor" href="#shu-xing" aria-hidden="true">¶</a> 属性</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定值</td>
<td style="text-align:left">string/number</td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">输入框尺寸</td>
<td style="text-align:left">string</td>
<td style="text-align:left">large/medium/small</td>
<td style="text-align:left">small</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">是否禁用</td>
<td style="text-align:left">boolean</td>
<td style="text-align:left">—</td>
<td style="text-align:left">false</td>
</tr>
<tr>
<td style="text-align:left">name</td>
<td style="text-align:left">原生 name 属性</td>
<td style="text-align:left">string</td>
<td style="text-align:left">—</td>
<td style="text-align:left">-</td>
</tr>
<tr>
<td style="text-align:left">label</td>
<td style="text-align:left">选中状态的值</td>
<td style="text-align:left">string/boolean</td>
<td style="text-align:left">—</td>
<td style="text-align:left">-</td>
</tr>
</tbody>
</table>
<h3 id="shi-jian"><a class="header-anchor" href="#shi-jian" aria-hidden="true">¶</a> 事件</h3>
<table class="table"><thead>
<tr>
<th style="text-align:left">事件名称</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">回调参数</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">change</td>
<td style="text-align:left">当绑定值变化时触发的事件</td>
<td style="text-align:left">(value: string | number, event: Event)</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
        data () {
            return {
                checked: false,
                checked1: false,
                checked2: true,
                disabled: true
            }
        }
    }
</script>


